@import './../../styles/mixins.scss';

.about {
  position: relative;
  width: 400px;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  gap: 10px;

  @include onPhone {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
    padding: 0 15px;
  }

  &__photo {
    width: 250px;
    height: 250px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border: none;
    border-radius: 15px;
    box-shadow: rgb(71, 71, 244) 0px 0px 0px 2px inset, rgba(255, 255, 255, 0) 10px 10px 0px -3px, rgb(31, 193, 27) -10px -10px, rgba(255, 255, 255, 0) 20px 20px 0px -3px, rgb(255, 217, 19) -20px -20px, rgba(255, 255, 255, 0) 30px 30px 0px -3px, rgb(255, 156, 85) -30px -30px, rgba(255, 255, 255, 0) 40px 40px 0px -3px, rgb(255, 85, 85) -40px -40px;
  
    @include onPhone {
      box-shadow: rgb(71, 71, 244) 0px 0px 0px 2px inset, rgba(255, 255, 255, 0) 10px 10px 0px 3px, rgb(31, 193, 27) 0 -10px, rgba(255, 255, 255, 0) 20px 20px 0px 3px, rgb(255, 217, 19) 0 -20px, rgba(255, 255, 255, 0) 30px 30px 0px 3px, rgb(255, 156, 85) 0 -30px, rgba(255, 255, 255, 0) 40px 40px 0px 3px, rgb(255, 85, 85) 0 -40px;
    }
  }

  &__info {
    width: 375px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    padding: 10px;

    @include onPhone {
      width: 100%;
    }
  }

  &__name {
    display: block;
    width: 100%;
    font-size: 70px;
    margin-bottom: 10px;

    @include onPhone {
      font-size: 50px;
      text-align: center;
    }
  }

  &__desc {
    font-family: Roboto, sans-serif;
    font-weight: 700;

    @include onPhone {
      text-align: justify;
    }
  }

  &__stack {
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: end;
    flex-wrap: wrap;
    gap: 3px;

    @include onPhone {
      width: 100%;
      flex-direction: row;
      justify-content: center;
      align-items: start;
      height: auto;
      margin-bottom: 10px;
    }

    & * {
      width: 35px;
      height: 35px;
    }
  }
}
